import classes from './FormsControl.module.scss'
import { Field } from 'redux-form'
import React from 'react'


export const FormControl = ({ meta: { touched, error }, children }) => {
  const hasError = touched && error
  
  return (<div className = { `${ classes.formControl } ${ hasError
      ? classes.error
      : '' }` }>
    { children }
    { hasError && <span>{ error }</span> }</div>)
}

export const Input = (props) => {
  return < FormControl { ...props }><input { ...props.input } { ...props } /></FormControl>
}

export const Textarea = (props) => {
  return < FormControl { ...props }><textarea { ...props.input } { ...props } /></FormControl>
}

export const createField = (component, name, placeholder, validators, props = {}, text = '') => {
  return <div className = { classes.field }>
    <Field component = { component }
        name = { name }
        placeholder = { placeholder }
        validate = { validators }
        { ...props } />{ text && <span>{ text }</span> }
  </div>
}