import classes from './AddNewPostForm.module.scss'
import { Field, reduxForm } from 'redux-form'
import { maxLengthCreator, requiredField } from '../../../../Utils/Validators/Validators.js'
import { Textarea } from '../../../Common/FormsControls/FormsControls.jsx'


const maxLength30 = maxLengthCreator (30)

const AddNewPostForm = (props) => {
  return (
      <form onSubmit = { props.handleSubmit } className = { classes.post_new }>
        <div>
          <Field component = { Textarea }
              validate = { [ requiredField, maxLength30 ] }
              name = 'newPostText'
              placeholder = 'my post...' />
        </div>
        <button>Send</button>
      </form>)
}

const PostFormRedux = reduxForm ({
  form: 'newPostText',
}) (AddNewPostForm)

export default PostFormRedux
