import classes from './UserProfile.module.scss'
import userPhoto from '../../../../assets/image/User.png'
import ProfileStatus from './ProfileStatus/ProfileStatus.jsx'
import { profileAPI } from '../../../../API/API.js'


const UserProfile = (props) => {
  
  let uploadPhoto = () => {
    let formData = new FormData ()
    let imagefile = document.querySelector ('#photo')
    formData.append ('image', imagefile.files[ 0 ])
    profileAPI.updatePhoto(formData)
        .then ((res => console.log (res.datA)))
  }
  
  return (<div className = { classes.wrapper }>
    <div className = { classes.user }>
      <div className = { classes.photo }>
        { props.photo
            ? <img src = { props.photo } alt = 'photo' />
            : <div ><img
                src = { userPhoto } alt = 'photo' />
              <input type = 'file' id = 'photo' />
              <button onClick={uploadPhoto}>add photo</button>
            </div> }
      </div>
      <div className = { classes.description }>
        <div className = { classes.name }>{ props.name }</div>
        <div className = { classes.about }>{ props.aboutMe }</div>
      </div>
    </div>
    <ProfileStatus status = { props.status }
        updateUserStatus = { props.updateUserStatus }
    />
  </div>)
}
export default UserProfile
