import classes from './MyPosts.module.scss'
import Post from './Post/Post'
import PostFormRedux from './AddNewPostForm/AddNewPostForm.jsx'
import React from 'react'


const MyPosts = React.memo (({posts,addPost}) => {
  
  let postElement = posts.map (p => (<Post
      text = { p.text }
      like = { p.likeCount }
      key = { p.id } />))
  
  let onAddPost = (value) => {
    addPost (value.newPostText)
  }
  
  return (<div className = { classes.wrapper }>
    <h3>My posts</h3>
    <PostFormRedux onSubmit = { onAddPost } />
    <div className = { classes.posts }>
      { postElement }
    </div>
  </div>)
})

export default MyPosts
